<script>
import { mapState, mapActions } from 'vuex'
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [priceMixin, loaderMixin],
    components:{
        DeleteModal
    },
    data(){
        return {
            page: 1,
            pageCount: 0,
            editModal: false,
            deleteModal: false,
            headers: [
                {text: 'Status', value: 'status'},
                {text: 'Qayerdan', value: 'direction'},
                {text: 'Kodi', value: 'code'},
                {text: 'Nomi', value: 'name',},
                {text: 'Massa', value: 'amount'},
                {text: 'Rangi', value: 'color_code'},
                {text: 'Sana', value: 'date'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
            items: [],
        }
    },
    watch: {
        // '$route.params.id': {
        //     handler: async function(value) {
        //         await this.refresh()
        //     },
        //     deep: true,
        //     immediate: true
        // },
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
        // update: async function (newVal, oldVal) {
        //     if (newVal) {
        //         await  this.refresh()
        //     }
        // },
    },

    async mounted() {
        await this.refresh();
    },
    computed: {
        ...mapState(
            {
                ready_raw: state => state.supply.ready_raw
            }
        ),
    },
    methods: {
        ...mapActions({
            fetchReadyRaw: 'supply/fetchReadyRaw'
        }),
        async refresh() {
            this.loading = true
            await this.fetchReadyRaw({page: this.page, factory: 7, type: 'painting', parent_id: 2});

            this.page = this.ready_raw.current_page;
            this.pageCount = Math.ceil(this.ready_raw.total / 10);
            this.items = this.ready_raw.data;

            this.items.forEach((elem) => {
                elem.direction = 'Bo`yoqxonadan';
                elem.code = elem.order.product.code
                elem.name = elem.order.product.name
                elem.color_code = elem.order.color_code
                elem.date = this.$dayjs(elem.created_at).format('DD-MM-YYYY') 
            })
            this.loading = false
        },
        // async updateTable(e){
        //     if(e){
        //         await this.refresh();
        //     }
        // },
        closeEditModal(){
            this.editModal = false
        },
    }
}
</script>

